


.Hero {
		background-image: url(./images/hero-bg1.jpg);
		background-position: right;
		background-size: cover;
		background-position-x: -16rem;
		background-repeat: no-repeat;
		display: grid;
		grid-template-columns: 1fr;
		height: 100vh;

}
	.Hero-nav {
		display: grid;
		grid-template-columns: 1fr 1fr;
		padding: 1.5rem;
	}
		.Hero-logo a {
			font-family: 'publicaslab-bold-webfont';
			font-size: 26px;
			text-decoration: none;
			letter-spacing: 2px;
			color: black;
		}

		.Hero-menu {
			justify-self: right;
		}
	
		.Hero-menu img {
			width : 26px;
		}

	.Hero-content {
		display: grid;
		grid-template-columns: 1fr ;
		grid-template-rows: 0.15fr 0.85fr;
		justify-items: center;
		-ms-flex-align: start;
		    align-items: flex-start;
	}
		.Hero-title a{
			color: #276A7B;
			font-family: 'publicaslab-bold-webfont';
			font-size: 42px;
			max-width: 100%;
			-webkit-box-sizing: border-box;
			        box-sizing: border-box;
			letter-spacing: 10px;
		}

		#thin {
			font-family: 'publicaslab-light-webfont';
			font-weight: 100;
		}

		.Hero-subtitle {
			font-family: 'Circular Std Book';
			font-size: 16px;
			letter-spacing: 2px;
		}



