@media only screen and (min-width: 780px){

	.Hero-nav {
		display: grid;
		grid-template-columns: 0.25fr 0.75fr;
		padding: 1.5rem;
		line-height: 30px;
	}

	.Hero-nav ul {

		list-style: none;
	}
	.Hero-nav ul li {
		font-family: 'SamsungSharpSansBold';
		color: #A8A8A8;
		float: right;
		padding: 12px;
	}

	.Hero-nav ul li:hover {
		text-decoration: underline;
	}

}



