/* css reset */


*, html {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: baseline;
}


body {
	width: 100%;
	background-color: #F5F5F5;
}

/* default font size */

h1 {
	font-size:68px;
}

h2 {
	font-size: 42px;
	font-family: 'CircularStd-Black';
}

h3 {
	font-size:26px;
}

p {
	font-family: 'SamsungSharpSansBold';
	font-size:16px;
	letter-spacing: 2px;
	line-height: 30px;
	color: #5D5D5D;
}


/* default orientation classes */

.right-align {
	text-align: right;
}


/* default font color */

muted {
	color : #5D5D5D;
}



