
	.Projects {
		display: grid;
		grid-template-columns: 1fr;
		padding : 3rem;
		padding-bottom: 50%;
		justify-items: center;
	}
	
			.Projects-top h2 {
				color: #E1A9A5;
				margin-bottom: 60px;
				width: -webkit-fit-content;
				width: -moz-fit-content;
				width: fit-content;
				text-align: center;
			}
			
			.Projects-top p {
				padding-top: 1.5rem;
			}

	
			.Projects-portfolio img {
				width : 20rem;
				max-width:100%;
				margin : 5rem 0 1rem 0;
			}


			.Projects-portfolio h2 {
				font-family: 'FuturaBT-Bold';
				letter-spacing: 4px;
				color: #5D5D5D;
			}


@media only screen and (min-width: 600px){

	.Projects {
		display: grid;
		grid-template-columns: 1fr;
		padding : 6rem;
		padding-bottom: 20%;
		justify-items: center;
	}
	
			.Projects-top h2 {
				color: #E1A9A5;
				margin-bottom: 60px;
				width: -webkit-fit-content;
				width: -moz-fit-content;
				width: fit-content;
				text-align: center;
			}
			
			.Projects-top p {
				padding-top: 1.5rem;
			}

	
			.Projects-portfolio img {
				width : 20rem;
				max-width:100%;
				margin : 5rem 0 1rem 0;
			}


			.Projects-portfolio h2 {
				font-family: 'FuturaBT-Bold';
				letter-spacing: 4px;
				color: #5D5D5D;
			}

}

	

@media only screen and (min-width: 1024px){
	.Projects {
		display: grid ;
		grid-template-columns: 1fr 1fr;
	}
	.Projects-top {
		margin-left: 50%;
		grid-column: 1 / 3;
	}
	.Projects-portfolio:nth-of-type(3) {
		grid-column: 2 / span 1 ;
	}

	.Projects-portfolio:nth-of-type(2) {
		grid-column: 1 / span 3 ;
		justify-self: left;
	}

}


