
	.Contact {
        background-color: #5D5D5D;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 5fr;
        height: 570px;
        padding : 1.3rem;
        padding-top: 2rem;
        -ms-flex-align: end;
            align-items: end;
    }

    .Contact-info {
        color: white;
        text-align: center;
    }

    .Contact-info p {
        font-family: 'CircularStd-Book';
        color: #DFEBF0;
    }

    .Contact-info h3 {
        font-family:  'CircularStd-Black';
        color:#DFEBF0;
        text-decoration: underline;
    }


        .Contact-info-details {
            padding : 3rem;
            padding-top: 6rem;
            text-align: left;
        }

        .Contact-info-details > h3 {
            font-family: 'SamsungSharpSansRegular';
            color: white;
            text-decoration: none;
        }

        .Contact-info-details > p {
            padding-bottom: 2rem;
            font-family: 'SamsungSharpSansRegular';
            color: white;
        }
        .Contact-footer {
            text-align: center;
        }
            .Contact-footer p {
                color : #A8A8A8;
            }


