@media only screen and (min-width: 1024px){
    .Approach {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
    .Approach-head {
        grid-column: 1 / span 2 ;

        width: 40%;
    }

    #first-li {
        margin-right: 0;
    }

