        font-family: 'FuturaBT-Bold';
        color: #5D5D5D;
        margin-bottom: 0;
    }

    .Approach-body {
        grid-column: 1 / span 3;
        width : 60%;
    }

    #second-li {
        grid-column: 1 / span 2;
    }


}


